@import "../../assets/style/valuable";
@media screen and (min-width: 830px) {
  .goUpBtn {
    color: $color;
    font-family: "Poppins", sans-serif;
    font-weight: bold;
    cursor: pointer;
    display: none;
    position: fixed;
    left: 100px;
    bottom: 40px;
    transition: 0.4s;
    &:hover {
      bottom: 43px;
    }
    &.on {
      display: block;
    }
  }
}
@media screen and (max-width: 829px) {
  .goUpBtn {
    color: $color;
    font-family: "Poppins", sans-serif;
    font-weight: bold;
    display: none;
    position: fixed;
    left: 20px;
    bottom: 35px;
    &:hover {
      bottom: 43px;
    }
    &.on {
      display: block;
    }
  }
}
